<template>
	<view class="main">
		<tou_hand1 handtitle='端口码管理'></tou_hand1>
		<view class="show">
			<view class="one">
				<input v-model="portCode" placeholder='端口码'/>
				<input v-model="posts" placeholder='端口号'/>
				<view class="onetap" @click="selbtn">搜索</view>
			</view>
			<view class="three">
				<view class="threetap" @click="addbtn">绑定总码</view>
				<view class="threetap" @click="dkbtn">绑定端口</view>
			</view>
			<view class="stwo">
				<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
					<van-list v-model="loading" :offset="300" :finished="finished" finished-text="没有更多了"
						@load="loadMore">
						<van-cell v-for="(item, index) in list" :key="index">
							<view class="twotap">
								<view class="twotapshang">
									<view>
										<view>设备号:{{item.code}}</view>
										<view>端口码:{{item.portCode}}</view>
									</view>
									<view class="twotapshangbtn" @click="delbtn(item.id)">删除</view>
								</view>
								<view class="twotapxia">
									<span style='margin-right: 20upx;'>端口号:{{item.ports}}</span>
									创建时间:{{item.createTime}}
								</view>
							</view>
							
						</van-cell>
					</van-list>
				</van-pull-refresh>
			</view>
		</view>	
	</view>
</template>

<script>
	import tou_hand1 from '../../components/tou_hand1/tou_hand1.vue'
	 
	import Toast from '../../wxcomponents/vant/toast/toast';
	import Dialog from '../../wxcomponents/vant/dialog/dialog';
	export default {
		components: {tou_hand1},
		mounted() {
			document.querySelector('body').setAttribute('style', 'background-color:#f5f5f5')
		},

		data() {
			return {
				list:[],
				loading: false,
				finished: false,
				refreshing: false,
				pageNum: 1, //页码
				pageSize: 10, //每页显示的条数
				deviceId:'',//16进制设备号
				portCode:'',//端口码
				posts:'',//端口号
				
			}
		},
		onShow() {
			this.list = [];
			this.onRefresh()
		},
		onLoad(options) {
			this.deviceId=options.deviceId
			
		},
		methods: {
			
			loadMore() {
			
				if (this.refreshing) {
					this.list = [];
					this.refreshing = false;
				}
				const that = this;
				let data = {
					pageNum: that.pageNum,
					pageSize: that.pageSize,
					deviceId:that.deviceId,
					portCode:that.portCode,
					ports:that.posts
				}
				that.loading = true;
				that.$base.request1('manager/portCode/list', 'GET', data)
					.then(res => {
						that.loading = false;
						if (res.data.rows && res.data.rows.length > 0) {
							var arr = res.data.rows
							for(var i=0;i<arr.length;i++){
								if(arr[i].ports!='FF'){
									arr[i].ports='0'+(Number(arr[i].ports)+1)
								}
							}
							that.list = that.list.concat(arr);
							that.pageNum += 1;
						} else {
							that.finished = true;
						}
					})
					.catch(err => {
			
					})
			},
			onRefresh() {
				// 清空列表数据
				this.finished = false;
				this.pageNum = 1
			 // 重新加载数据
				// 将 loading 设置为 true，表示处于加载状态
				this.loading = true;
				this.loadMore();
			},
			addbtn(){
				let _this = this;
				let data = {
					url: location.href.split('#')[0],
					appid: uni.getStorageSync('user').appid
				}
				_this.$base.request('configs', 'GET', data)
					.then(res => {
						if (res.data.code == '200') {
							var timestamp = res.data.data.timestamp;;
							var noncestr = res.data.data.noncestr;
							var signature = res.data.data.signature;
							var appId = res.data.data.appid;
							wx.config({
								debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
								//debug : true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
								appId: appId, // 必填，公众号的唯一标识
								timestamp: timestamp, // 必填，生成签名的时间戳
								nonceStr: noncestr, // 必填，生成签名的随机串
								signature: signature, // 必填，签名，见附录1
								jsApiList: ['scanQRCode', ] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
							});
				
							wx.ready(function() {
								wx.scanQRCode({
									needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
									scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
									success: function(res1) {
										var dk = res1.resultStr.split('/')
										console.log('端口号：', dk[4])
										
										if (dk[4].substr(-2, 2) == '00') {
											_this.jkbtn(dk[4]);
										} else {
											_this.$toast.fail('请扫描正确二维码')
											return
										}
										
				
				
									}
								});
							});
						} else {
							_this.$toast.fail(res.data.msg)
							return
						}
				
					})
					.catch(err => {
				
					})
			},
			jkbtn(portDevice){
				let data = {
					deviceId: this.deviceId,
					portDevice: portDevice,
					ports:'FF'
				}
				this.$base.request1('manager/portCode/add', 'POST', data)
				.then(res => {
					if (res.data.code == '200') {
						this.$toast.success('添加成功')
						this.list = [];
						this.onRefresh()
						this.addbtn()
					}else{
						this.$toast.fail(res.data.msg)
						return
					}
				})
				.catch(err => {
						
				})
			},
			dkbtn(){
				uni.navigateTo({
					url: '/pages/port_list/port_list?deviceId=' + this.deviceId
				});
			},
			delbtn(id){
				Dialog.confirm({
					title: '标题',
					message: '确认删除？'
				}).then(() => {
					this.$base.request1('manager/portCode' + '/' + id, 'DELETE')
					.then(res => {
						if (res.data.code == '200') {
							this.list = [];
							this.$toast.success('删除成功')
							this.onRefresh()
						}else{
							this.$toast.fail(res.data.msg)
							return
						}
					})
					.catch(err => {
							
					})
				}).catch(() => {
					// on cancel
				});
				
			},
			selbtn(){
				this.list = [];
				this.onRefresh()
			}
			
		}
	}
</script>

<style lang="scss" scoped>
	.main {
		width: 100%;

		/deep/.van-list__finished-text {
			line-height: 500upx;
		}
		.show {
			width: 100%;
			box-sizing: border-box;
			/deep/.van-popup {
				border-radius: 16upx;
			}
			.one{
				width: 650upx;
				box-sizing: border-box;
				display: flex;
				align-items: center;
				margin: 32upx auto;
				input{
					width: 200upx;
					height: 64upx;
					border: 1upx solid #b0b0b0;
					padding-left: 20upx;
					border-radius: 10upx;
					margin-right: 20upx;
					font-size: 28upx;
				}
				.uni-input-placeholder{
					font-size: 28upx;
				}
				.onetap{
					width: 150upx;
					box-sizing: border-box;
					padding: 12upx 0;
					background-color: #4B98ED;
					color: #fff;
					text-align: center;
					border-radius: 10upx;
					margin-left: 20upx;
					font-size: 28upx;
				}
			}
			.stwo{
				width: 650upx;
				box-sizing: border-box;
				margin: 0 auto;
				/deep/.van-cell{
					border-radius: 20upx;
					margin-bottom: 20upx;
					display: flex;
				}
				.twotap{
					padding: 15upx 10upx;
					.twotapshang{
						font-size: 30upx;
						font-family: PingFang SC Bold, PingFang SC Bold-Bold;
						font-weight: 700;
						color: #333333;
						box-sizing: border-box;
						padding: 10upx 20upx;
						display: flex;
						justify-content: space-between;
						align-items: center;
						.twotapshangbtn{
							width: 120upx;
							box-sizing: border-box;
							padding: 8upx 0;
							background-color: red;
							color: #fff;
							text-align: center;
							border-radius: 10upx;
							font-size: 28upx;
						}
					}
					.twotapxia{
						font-size: 26upx;
						font-family: PingFang SC Bold, PingFang SC Bold-Bold;
						font-weight: 700;
						color: #686868;
						box-sizing: border-box;
						padding:0 20upx;
						padding-bottom: 10upx;
					}
				}
			}
			.three{
				width:650upx;
				margin: 0 auto;
				margin-bottom: 20upx;
				display: flex;
				justify-content: space-between;
				background-color: #fff;
				box-sizing: border-box;
				padding: 25upx 140upx;
				border-radius: 10upx;
				.threetap{
					width: 150upx;
					box-sizing: border-box;
					padding: 12upx 0;
					background-color: #4B98ED;
					color: #fff;
					text-align: center;
					border-radius: 10upx;
					font-size: 28upx;
				}
			}
			
		}

	}
</style>
